.room_container {
    height: 100vh;
    width: 100vw;
    position: relative;
}
.room_container>div{
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.room_container>div>iframe{
    height: 100%;
    width: 100%;
    overflow: hidden;
}
.room_container>iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.room_container_wrapper {
    height: 100%;
    width: 100%;
    padding: 0.2%;
    position: fixed;
    top: 0;
    left: 0;
}

.header_container {
    height: fit-content;
    width: 100%;
}

.header_container_wrapper {
    justify-content: space-between;
    padding: 0 0 0 calc(1vh + 1vw);
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
    float: right;
}
.list_item_container{
    justify-content: space-between;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    flex-wrap: nowrap;
}
.list_item_container>ul {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    list-style: none;
}

.list_item_container>li {
    margin: 0;
    height: 100%;
    width: 100%;
}
.list_item_container button {
    border: none;
    background-color: transparent;
    font-size: 16px;
    cursor: pointer;
}
.back_arrow_icon{
    font-size: calc(3vh + 3vh);
    color: aliceblue;
    cursor: pointer;
    position: absolute;
    left: 0;
    top: 5%;
}
.back_arrow_icon:hover{
    color: rgb(186, 187, 188);
    margin-left: 0.5vw;
}

.header_icon {
    background-color: transparent;
    color: white;
    margin-right: 1vw;
    position: relative;
    display: inline-block;
}
.header_icon:hover {
    opacity: 0.7;
}
.header_container_wrapper li:hover .icon_description {
    display: block;
    position: fixed;
}
.icon_description {
    display: none;
    line-break: auto;
    background-color: transparent;
    color: rgb(147, 148, 149);
    font-style: italic;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    font-weight: bold;
}